.todo-list-container {
  background: var(--surface-color);
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0 4px 20px var(--shadow-color);
  border: 1px solid var(--border-color);
}

.todo-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.empty-state {
  text-align: center;
  padding: 3rem 2rem;
  color: var(--text-secondary);
}

.empty-state p {
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
  font-style: italic;
}

.todo-stats {
  border-top: 1px solid var(--border-color);
  padding-top: 1.5rem;
  text-align: center;
}

.todo-stats p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 500;
}

@media (max-width: 768px) {
  .todo-list-container {
    padding: 1.5rem;
  }
  
  .empty-state {
    padding: 2rem 1rem;
  }
  
  .empty-state p {
    font-size: 1rem;
  }
} 