.footer {
  background: var(--surface-color);
  border-top: 1px solid var(--border-color);
  padding: 1.5rem 0;
  margin-top: auto;
  box-shadow: 0 -2px 10px var(--shadow-color);
}

.footer-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
  text-align: center;
}

.footer-content p {
  margin: 0.25rem 0;
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 400;
}

.footer-content p:first-child {
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

@media (max-width: 768px) {
  .footer {
    padding: 1rem 0;
  }
  
  .footer-content p {
    font-size: 0.8rem;
  }
} 