.todo-item {
  background: var(--surface-color);
  border: 1px solid var(--border-color);
  border-radius: 0.75rem;
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px var(--shadow-color);
}

.todo-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px var(--shadow-color);
}

.todo-item.completed {
  opacity: 0.7;
  background: linear-gradient(135deg, var(--surface-color), rgba(72, 187, 120, 0.1));
}

.todo-item.editing {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.todo-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
}

.todo-checkbox {
  width: 24px;
  height: 24px;
  border: 2px solid var(--border-color);
  border-radius: 50%;
  background: var(--surface-color);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.875rem;
  color: white;
  flex-shrink: 0;
}

.todo-checkbox:hover {
  border-color: var(--primary-color);
  transform: scale(1.1);
}

.todo-checkbox.checked {
  background: var(--success-color);
  border-color: var(--success-color);
}

.todo-text-container {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  flex: 1;
}

.todo-text {
  font-size: 1rem;
  color: var(--text-primary);
  font-weight: 500;
  word-break: break-word;
  line-height: 1.4;
}

.todo-item.completed .todo-text {
  text-decoration: line-through;
  color: var(--text-secondary);
}

.todo-date {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 400;
}

.todo-edit-input {
  flex: 1;
  padding: 0.5rem 1rem;
  border: 2px solid var(--primary-color);
  border-radius: 0.5rem;
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;
  color: var(--text-primary);
  background: var(--surface-color);
  outline: none;
  transition: all 0.3s ease;
}

.todo-edit-input:focus {
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.todo-actions {
  display: flex;
  gap: 0.5rem;
  flex-shrink: 0;
}

.todo-action-btn {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  background: var(--surface-color);
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  box-shadow: 0 2px 4px var(--shadow-color);
}

.todo-action-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 8px var(--shadow-color);
}

.edit-btn:hover {
  background: var(--warning-color);
  color: white;
}

.delete-btn:hover {
  background: var(--danger-color);
  color: white;
}

.save-btn:hover {
  background: var(--success-color);
  color: white;
}

@media (max-width: 768px) {
  .todo-item {
    padding: 1rem;
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }
  
  .todo-content {
    gap: 0.75rem;
  }
  
  .todo-actions {
    justify-content: center;
  }
  
  .todo-action-btn {
    width: 40px;
    height: 40px;
    font-size: 1.1rem;
  }
} 